/* eslint-disable no-underscore-dangle */
import { Component, Input, OnInit } from '@angular/core';
import { LoadingController, ModalController } from '@ionic/angular';
import { ApiService } from 'src/app/services/api.service';
import { ToastController } from '@ionic/angular';
import { CommonService } from 'src/app/services/common.service';

@Component({
  selector: 'app-view-query',
  templateUrl: './view-query.page.html',
  styleUrls: ['./view-query.page.scss'],
})
export class ViewQueryPage implements OnInit {
  @Input() query;
  role;
  photos: any[] = [];
  loader;
  constructor(
    public modalController: ModalController,
    private api: ApiService,
    public toastController: ToastController,
    private common: CommonService,
    public loadingController: LoadingController
  ) {}

  ngOnInit() {}
  ionViewDidEnter() {
    this.photos = this.query.photos;
    this.role = this.common.getUser()?.role.id;
    console.log(this.role);
  }
  closeModal() {
    this.modalController.dismiss({
      dismissed: true,
    });
  }
  async presentToast(msg, toastClass) {
    await this.toastController
      .create({
        message: msg,
        cssClass: toastClass,
        duration: 2000,
      })
      .then((res) => res.present());
  }

  async presentLoading() {
    await this.loadingController
      .create({
        cssClass: 'my-custom-class',
        message: 'Please wait...',
      })
      .then((res) => {
        this.loader = res;
        res.present();
      });
  }

  updateIssueStatus(resolved) {
    if (resolved) {
      this.query.resolved = resolved;
      this.query.dateResolved = new Date();
      this.presentLoading();
      this.api
        .updateQueryStatus(
          'query/updateQueryStatus/' + this.query._id,
          this.query
        )
        .subscribe((res) => {
          if (!res?.err) {
            this.loader.dismiss();
            this.presentToast('Query Marked As Reolved', 'success');
            setTimeout(() => {
              window.location.reload();
            }, 2200);
          } else {
            this.loader.dismiss();
            this.presentToast(res?.err, 'failed');
          }
        });
    }
  }
}
